.btn {
	@extend %button;
	color: #fff;
	line-height: 1;
	background-color: $blue;
	@include roboto-bold;
	padding: 17px 19px;
	font-size: 14px;
	@include hover {
		background-color: lighten($blue, 10%); }
	@include active {
		background-color: darken($blue, 5%); } }
.btn-primary {
	@extend %button;
	background-color: $pink;
	padding: 27px 60px;
	color: #fafafc;
	font-size: 20px;
	@include robotomono-bold;
	@include md {
		font-size: 16px;
		padding: 24px 34px; }
	@include hover {
		background-color: lighten($pink, 5%); }
	@include active {
		background-color: darken($pink, 5%); }
	&_blue {
		background-color: $blue;
		&:hover,
		&:focus {
			background-color: lighten($blue, 10%); }
		&:active {
			background-color: darken($blue, 5%); } } }
.btn-secondary {
	@extend %button;
	line-height: 1;
	color: #0ca8fd;
	@include css-lock(16, 20, 900, 1920);
	@include robotomono-bold;
	border: 2px solid #0ca8fd;
	border-radius: 37px;
	padding: 25px 70px;
	overflow: hidden;
	@include hover {
		border-color: #fb1d95;
		.btn-secondary__in {
			right: 0; } }
	&__in {
		position: absolute;
		top: -2px;
		bottom: -2px;
		width: 100%;
		top: 0;
		right: 120%;
		background-color: #fb1d95;
		display: block;
		transition: right 0.3s; }
	&__text {
		position: relative;
		z-index: 2; } }
.btn-menu {
	@include hide-text;
	width: 28px;
	height: 20px;
	position: relative;
	z-index: 1;
	overflow: hidden;
	top: 1px;
	background-color: transparent;
	display: inline-block;
	vertical-align: middle;
	margin-left: 14px;
	span {
		position: absolute;
		left: 0;
		right: 0;
		top: 50%;
		margin-top: -1px;
		height: 3px;
		background-color: #fff;
		display: block;
		&::after,
		&::before {
			content: '';
			display: block;
			position: absolute;
			height: 3px;
			background-color: #fff;
			left: 0;
			right: 0; }
		&::before {
			top: -8px; }
		&::after {
			top: 8px; } }
	@include hover {
		.sidebar {
			@include gpu; } } }
.btn-close {
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto;
	right: 5px;
	background: transparent;
	@include roboto-med;
	font-size: 14px;
	color: #0ca8fd;
	padding-right: 32px;
	transition: color 0.3s;
	.icon {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		transition: fill 0.3s; }
	@include hover {
		color: lighten(#0ca8fd, 15%);
		.icon {
			fill: lighten(#0ca8fd, 15%); } } }
.btn-main {
	@extend %button-basic;
	padding: 21px 17px;
	min-width: 194px;
	border: 2px solid white;
	border-radius: 5px;
	@include roboto-bold;
	@include css-lock(22, 30, 900, 1920);
	line-height: 1;
	color: #fff;
	background: transparent;
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: -2px;
		right: -2px;
		top: 100%;
		margin-top: -1px;
		height: 6px;
		border-bottom-left-radius: 8px;
		border-bottom-right-radius: 8px;
		background-color: #fff; }
	@include hover {
		&:not([disabled]) {
			top: 5px;
			&:after {
				display: none; } } }
	@include active {
		&:not([disabled]) {
			top: 5px;
			&:after {
				display: none; } } } }
.btn-category {
	@extend %button-basic;
	min-width: 282px;
	text-transform: lowercase;
	@include roboto-bold;
	font-size: 30px;
	line-height: 1;
	color: rgba(#fff, 0.4);
	word-break: break-word;
	background: transparent;
	@include hover {
		color: rgba(#fff, 1);
		&:before {
			width: 100%; }
		&:after {
			height: 100%; } }
	@include sm {
		margin-top: 40px; }
	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		border-style: solid;
		border-color: #fff;
		transition: all 0.3s;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0; }
	&:before {
		width: 0;
		border-width: 4px 0 4px 0; }
	&:after {
		border-width: 0 4px 0 4px;
		height: 0; }
	&:nth-of-type(odd) {
		margin-right: 28px; }
	span {
		position: relative;
		z-index: 2; }
	&__inner {
		border: 4px solid rgba(#fff, 0.4);
		padding: 26px 15px;
		@include sm {
			padding: 20px; } } }
.btn-back {
	@extend %button-basic;
	font-size: 24px;
	color: rgba(#fff, 0.4);
	@include trans;
	padding-left: 30px;
	line-height: 1.4;
	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		transition: all 0.3s;
		@include arr(14, 20, rgba(#fff, 0.4), l); }
	@include hover {
		color: #fff;
		&:before {
			border-right-color: #fff; } } }
.btn-variation {
	@extend %button;
	background: transparent;
	min-width: 162px;
	padding: 10px 15px;
	color: #fff;
	border: 4px solid #fff;
	@include roboto-bold;
	font-size: 24px;
	line-height: 1;
	text-transform: none;
	@include trans;
	@include hover {
		border-color: rgba(#fff, 0.4);
		color: rgba(#fff, 0.4); }
	&:active {
		top: 1px; } }
.btn-change {
	@extend %button-basic;
	font-size: 24px;
	color: rgba(#fff, 0.4);
	@include trans;
	padding-right: 50px;
	line-height: 1.4;
	@include hover {
		color: #fff;
		.icon {
			fill: #fff; } }
	.icon {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		@include trans; } }
