.popup {
	position: fixed;
	padding: 60px;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 300;
	overflow-x: hidden;
	overflow-y: auto;
	visibility: hidden;
	opacity: 0;
	transition: all 0.3s;
	background-color: $violet;
	&.is-active {
		visibility: visible;
		opacity: 1; }
	@include vertical;
	@include md {
		padding: 60px 20px; }
	&__window {
		display: inline-block;
		vertical-align: middle;
		max-width: 808px;
		width: 100%;
		box-shadow: 0 0 21px #000000;
		border: 1px solid #29293c;
		background-color: #1b1b28; }
	&__body {
		font-size: 16px; }
	&__caption {
		padding: 22px 0;
		display: table;
		width: 100%;
		font-size: 14px;
		line-height: 1.2; }
	&__top {
		background-color: #181823;
		text-align: left;
		padding: 9px 44px;
		position: relative; }
	&__top-title {
		font-size: 30px;
		@include roboto-light; }
	&__row {
		background-color: #1B1B28;
		padding: 12px 0px;
		display: table;
		width: 100%;
		&:nth-child(even) {
			background-color: #181823; } }
	&__col,
	&__caption-col {
		display: table-cell;
		vertical-align: middle;
		text-align: left;
		&_1 {
			width: 46%;
			padding-left: 44px; }
		&_2 {
			width: 35%; }
		&_3 {
			width: 19%;
			padding-right: 52px; } } }
.product {
	display: table;
	&__media {
		display: table-cell;
		vertical-align: middle;
		width: 36px;
		height: 36px;
		overflow: hidden;
		img {
			display: block;
			max-width: 100%;
			max-height: 100%;
			margin: auto; } }
	&__desc {
		display: table-cell;
		vertical-align: middle;
		padding-left: 22px; } }
.flags {
	font-size: 0;
	i + i {
		margin-left: 16px; } }
.popup-reg {
	&__window {
		display: inline-block;
		vertical-align: middle;
		max-width: 864px;
		width: 100%; }
	&__top {
		text-align: left; }
	&__body {
		padding-top: 126px; }
	&__sub {
		padding-top: 8px; }
	&__title {
		font-size: 48px;
		@include sm {
			font-size: 32px; } } }
.form {
	position: relative;
	&__bottom {
		text-align: right;
		margin-top: 40px; }
	&__row {
		position: relative;
		@include clr; }
	&__row_required {
		&:before {
			content: '*';
			font-size: 30px;
			line-height: 1;
			position: absolute;
			top: 15px;
			right: 100%;
			margin-right: 14px;
			color: rgba(#fff, 0.4); } }
	&__capcha {
		margin-top: 31px; }
	&__col-1 {
		width: 37%;
		float: left; }
	&__col-2 {
		width: 59%;
		float: right; } }
.form-step-1 {
	.form__row {
		counter-reset: list;
		&:not(:first-of-type) {
			padding-top: 60px; }
		.btn-category {
			&__inner {
				&:before {
					counter-increment: list;
					content: '0' counter(list);
					position: absolute;
					top: 0;
					left: 20px;
					z-index: 1;
					background-color: $violet;
					font-size: 18px;
					padding: 0 18px;
					margin-top: -6px; } } } }
	&__info {
		@include roboto-bold;
		font-size: 24px;
		max-width: 380px;
		text-align: right;
		display: inline-block;
		position: absolute;
		right: 0;
		top: -210px;
		@include sm {
			top: -263px; } } }
.form-step-2 {
	.form__row {
		&:not(:first-of-type) {
			margin-top: 12px; } } }
.field {
	position: relative;
	border-bottom: 4px solid rgba(255, 255, 255, 0.4);
	&.is-error {
		.error {
			display: inline-block; } } }
.input {
	background: none;
	@include roboto-bold;
	font-size: 30px;
	line-height: 1.2;
	padding: 12px 0;
	display: block;
	width: 100%;
	color: #fff;
	@include placeholder-color(rgba(#fff, 0.4)); }
.error {
	display: none;
	padding: 10px 28px;
	background-color: #eb2121;
	border-radius: 19px;
	@include roboto-bold;
	font-size: 16px;
	color: #fff;
	@include trans; }
.control {
	position: absolute;
	top: -226px;
	right: 0;
	text-align: right;
	&__bottom {
		padding-top: 22px; } }
.select-default {
	display: none; }
.select {
	position: relative;
	@include roboto-bold;
	font-size: 30px;
	position: relative;
	border-bottom: 4px solid #fff;
	&.is-open .select__list {
		display: block; } }

.select__text {
	background-color: transparent;
	color: #fff;
	padding: 9px 0;
	display: block;
	position: relative;
	cursor: pointer;
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		margin-top: -3px;
		right: 20px;
		border: 6px solid transparent;
		border-top: 6px solid #fff;
		z-index: 1;
		user-select: none; } }
.select__list {
	display: none;
	list-style: none;
	position: absolute;
	left: 0;
	right: 0;
	top: 100%;
	margin-top: 4px;
	box-shadow: 0 0 60px rgba(1, 2, 4, 0.13);
	overflow-x: hidden;
	overflow-y: auto;
	z-index: 10; }
.select li {
	padding: 8px 20px;
	cursor: pointer;
	background-color: $violet;
	transition: background-color 0.3s;
	font-size: 24px;
	color: rgba(#fff, 0.4);
	@include hover {
		color: #fff; } }
